header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-shrink: 0;
  box-sizing: border-box;
  width: 100%;
  height: 40px;
  padding: 5px;
  background: rgb(85, 85, 85);
  z-index: 2;
  -webkit-transition: background 0.3s;
  transition: background 0.3s;
}

.home {
  display: inline-flex;
  justify-content: center;
  height: 100%;
  width: 100%;
  background: rgb(238, 238, 238);
  overflow: auto;
}

.header-title {
  display: inline-flex;
  align-items: center;
  color: rgb(255, 255, 255);
  font-size: 22px;
  font-weight: 700;
  text-decoration: none;
}

.header-right-side {
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-sizing: border-box;
  height: 100%;
  color: rgb(255, 255, 255);
}

.signout-link {
  padding: 3px 5px 1px;
  margin-left: 12px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  color: rgb(255, 255, 255);
  background: rgba(0, 0, 0, 0.2);
  text-decoration: none;
}

