/* Typography */

h1 {
  font-size: 28px;
  font-weight: 300;
}

h5 {
  font-weight: 500;
  line-height: 1.7em;
}

h6 {
  color: #666;
  font-size: 14px;
}

p {
  margin: 16px 0;
  padding: 0;
}

/* notes Layout */

.app-layout {
  display: flex;
  margin: 10px;
}

.main-container {
  padding: 20px 18px;
}

.left-panel {
  flex: 20% 1;
  background: rgb(77, 57, 75);
}

.right-panel {
  flex: 80% 1;
  display: flex;
  padding: 10px 0 0 !important;
}

.panel {
  border: 1px solid rgb(221, 221, 221);
  margin: 0 4px;
}

.panel h2 {
  font-size: 18px;
  margin-left: 10px;
}

.panel .heading {
  padding-left: 20px;
  color: #fff;
  font-size: 20px;
  font-weight: bold;
}

.All-Threads {
  padding: 4px 21px;
  font-size: 17px;
  font-weight: bold;
  color: rgb(255, 255, 255) !important;
}

.All-Threads a {
  text-decoration: none;
  color: rgb(255, 255, 255);
}

.channel-heading {
  color: rgb(255, 255, 255);
  font-size: 18px;
  text-align: center;
  background: rgb(100, 136, 100);
  padding: 5px;
  margin-top: 0;
}

li {
  padding: 5px;
  color: rgb(242, 242, 242);
  font-size: 18px;
  border-bottom: rgb(255, 255, 255);
  text-decoration: none;
  color: rgb(255, 255, 255);
}

.send-input {
  width: 89%;
  padding: 1em;
  outline: none;
}

.channel-add-container {
  text-align: center;
}

.channels {
  margin-bottom: 24px;
}

.channel-input {
  width: 80%;
  padding: 5px;
}

.add-channelbtn {
  font-size: 15px;
  color: rgb(255, 255, 255);
  text-decoration: none;
  margin-top: 11px;
  display: grid;
}

.box {
  color: rgb(255, 255, 255);
  padding: 0;
}

.message-container {
  flex: 100% 1;
  display: flex;
  flex-direction: column;
}

.messages {
  flex: 1 1;
}

.link-style {
  background: rgb(100, 136, 100);
  text-transform: uppercase;
  padding: 13px 18px;
  height: auto;
  width: auto;
  text-decoration: none;
  color: rgb(255, 255, 255);
  font-size: 17px;
  font-weight: bold;
  margin: 0;
  float: right;
}

.incoming_msg_img {
  display: inline-block;
  width: 6%;
}

.received_msg {
  display: inline-block;
  padding: 0 0 0 10px;
  vertical-align: top;
  width: 92%;
}

.received_withd_msg {
  width: 57%;
}

.received_withd_msg p {
  margin: 8px 0;
  padding: 0;
}

.incoming_msg_img img {
  width: 100%;
}

.incoming_msg {
  background: rgb(199, 162, 162);
  margin: 0 30px 7px 17px;
  padding: 11px;
  border-radius: 10px;
  color: rgb(255, 255, 255);
}

.listandcard {
  background-color: rgb(49, 121, 186);
  overflow-y: hidden;
  color: rgb(57, 57, 57);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  height: 100vh;
  padding: 5px;
}

.horizontal-scroll-container {
  white-space: nowrap;
}

.single-list {
  background-color: rgb(227, 227, 227);
  position: relative;
  display: inline-flex;
  height: auto;
  max-height: 90%;
  flex-direction: column;
  border-radius: 3px;
  margin: 5px;
  padding: 10px;
}

.cards-container {
  overflow-y: auto;
  min-width: 250px;
  overflow-x: hidden;
  align-self: center;
  max-height: 90vh;
  padding-bottom: 30px;
  margin-top: 10px;
  flex-direction: column;
  justify-content: space-between;
  flex: 1 1;
}

.card {
  background-color: rgb(255, 255, 255);
  position: relative;
  cursor: pointer;
  max-width: 250px;
  margin-bottom: 7px;
  min-width: 230px;
  border-radius: 3px;
  border-bottom: 1px solid rgb(204, 204, 204);
  padding: 10px;
}

.card-title {
  margin-bottom: 10px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding-bottom: 6px;
  color: rgb(0, 0, 0);
  border-bottom: 1px solid rgb(238, 238, 238);
}

.card-detail {
  font-size: 12px;
  color: rgb(77, 77, 77);
  white-space: normal;
}

.input {
  box-sizing: border-box;
  width: 100%;
  padding: 4px;
  border: 0;
  border-radius: 3px;
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  font-weight: 700;
  overflow: hidden;
  resize: none;
}

.addnew-card-container {
  margin: 5px;
  margin-bottom: 20px;
  padding: 8px;
  border-radius: 3px;
  color: rgb(68, 68, 68);
  font-size: 16px;
  background: #fff;
}

.inputclass {
  border-bottom: 1px solid rgb(238, 238, 238);
}

.addbutton {
  color: rgb(131, 140, 145);
  display: block;
  cursor: pointer;
  bottom: 3px;
  border-radius: 0px 0px 3px 3px;
  padding: 5px 2px;
  text-decoration: none;
}

.addNew-list {
  background-color: rgb(43, 106, 163);
  position: relative;
  display: inline-flex;
  height: auto;
  flex-direction: column;
  border-radius: 3px;
  margin: 5px;
  padding: 5px;
}

.btn-newlist {
  color: rgb(255, 255, 255);
  min-height: 32px;
  vertical-align: top;
  margin-top: 0px;
  margin-right: 0px;
  font-size: 13px;
  cursor: pointer;
  margin-bottom: 0px;
  background: rgb(43, 106, 163);
  border-width: medium;
  border-width: initial;
  border-style: none;
  border-color: currentColor;
  border-color: initial;
  -webkit-border-image: none;
          border-image: none;
  -webkit-border-image: initial;
          border-image: initial;
  -webkit-transition: background 0.3s ease 0s;
  transition: background 0.3s ease 0s;
  padding: 4px 16px;
  border-radius: 4px;
}

